@import "mixin";
@import "variables";

.products {
    width: $mobilePercent;
    margin: 0 auto;
    @media (min-width: $tabletWidth) {
        width: $tabletPercent;
    }
    @media (min-width: $desktopWidth) {
        width: $desktopPercent;
    }
    padding-top: 21px;
    .title {
        margin-bottom: 41px;
    }
}
