@import "mixin";
@import "variables";

.cards {
    width: 100%;
    &__list {
        @media (min-width: $desktopWidth) {
            margin: 82px auto 71px auto;
            width: $desktopPercent;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            column-gap: 0;
        }
    }
}
